.profile__avatar {
  border-radius: 50%;
  width: 120px;
  height: 120px;
  margin: 0;
  padding: 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

@media screen and (max-width: 750px) {
  .profile__avatar {
    align-self: center;
  }
}

